  import urls from '../../utils/urls.js'
  export default {
    data () {
      return {
        version: '0.0.0',
				isacceptnews: "",
				isLogin:this.$store.state.isLogin,
      }
    },
    onLoad() { 
			  var uid = this.$store.state.userInfo.id;
		     if(uid){
						uni.request({
					  url: urls.HTTP + '/index/getacceptnews',
					  method: 'POST',
					  data: {
					    uid: uid,
					  },
					  success: (res) => {
					   this.isacceptnews = res.data.user.isacceptnews
					   
					  }
					}) 
				 }
			 // this.version = plus.runtime.version
      
		
    },
    methods: {
      changeUser: function () {
        uni.showModal({
          title: '提示',
          content: '确定要退出登录吗？',
          success(res) {
            if (res.confirm) {
              uni.removeStorageSync('user')
							uni.reLaunch({
								url:'../common/login'
							})
							uni.removeStorageSync('userInfo')
							this.$store.dispatch('isLogin', false)
            }
          }
        })
      },
      switchChange: function (e) {
				let _this = this
        if (e.detail.value) {
          var isacceptnews = 0
        } else {
          isacceptnews = 1
        }
        // let uid = uni.getStorageSync('user').id
				// let uid = JSON.parse(uni.getStorageSync('userInfo')).id
				let id = '';
				if(_this.isLogin){
					id = JSON.parse(uni.getStorageSync('userInfo')).id
				}else{
					 id = 0
				}
        console.log(isacceptnews)
        uni.request({
          url: urls.HTTP + '/user/edituser',
          method: 'POST',
          data: {
            id: id,
            isacceptnews: isacceptnews
          },
          success: (res) => {
            // console.log(res)
            uni.setStorageSync('user', res.data.user)
            this.$store.dispatch('userInfo', res.data.user)
          }
        })
      }
    },
    computed: {
      // userInfo () {
      //   return JSON.parse(uni.getStorageSync('userInfo')).id
      // },
      urlHttp () {
        return urls.UrlHttp
      }
    }
  }
